<script>
import { login, checkLogin, getUserInfo } from '@/utils/auth.js'
import { createTabSwipeMixin } from '@/utils/tabSwipe.js'

export default {
	mixins: [createTabSwipeMixin(3)],
	data() {
		return {
			userInfo: {
				name: 'Êú™ÁôªÂΩï',
				role: 'ËØ∑ÂÖàÁôªÂΩï',
				roleText: 'ËØ∑ÂÖàÁôªÂΩï',
				department: 'Âåó‰∫¨Ê¨¢‰πêË∞∑ÂåªÂä°ÂÆ§',
				avatarUrl: '',
				wechatAvatarUrl: ''
			},
			lastUpdateTime: '',
			isLoading: false,
			isLoggedIn: false,
			avatarLoadError: false,
			// È¶ñÊ¨°ÁôªÂΩïÁªëÂÆöÂæÆ‰ø°Âè∑ÂºπÁ™ó
			showWechatBindDialog: false,
			wechatIdInput: ''
		}
	},
	
	computed: {
		// ËÆ°ÁÆóÊòæÁ§∫ÁöÑÂ§¥ÂÉè
		displayAvatar() {
			if (this.avatarLoadError) {
				return ''
			}
			// ‰ºòÂÖàÁ∫ßÔºöËá™ÂÆö‰πâÂ§¥ÂÉè > ÂæÆ‰ø°Â§¥ÂÉè
			return this.userInfo.avatarUrl || this.userInfo.wechatAvatarUrl || ''
		}
	},
	
	onLoad() {
		console.log('===== ÊàëÁöÑÈ°µÈù¢ onLoad =====')
