		this.checkAndLogin()
	},
	onShow() {
		// ÊØèÊ¨°ÊòæÁ§∫È°µÈù¢Êó∂Ê£ÄÊü•ÁôªÂΩïÁä∂ÊÄÅ
		this.checkAndLogin()
	},
	methods: {
		// Ê£ÄÊü•Âπ∂ÁôªÂΩï
		async checkAndLogin() {
			console.log('checkAndLogin Ë∞ÉÁî®')
			// Ê£ÄÊü•ÊòØÂê¶Â∑≤ÁôªÂΩï
			const isLogin = checkLogin()
			const userInfo = getUserInfo()
			console.log('Êú¨Âú∞ÁºìÂ≠òÁôªÂΩïÁä∂ÊÄÅ:', { isLogin, userInfo })
			
			if (isLogin && userInfo) {
				// Â∑≤ÁôªÂΩïÔºåÂä†ËΩΩÁî®Êà∑‰ø°ÊÅØ
				this.isLoggedIn = true
				this.loadUserInfo()
				return
			}
			
			// Êú™ÁôªÂΩïÔºåÂ∞ùËØïËá™Âä®ÁôªÂΩï
			this.isLoggedIn = false
			this.isLoading = true
			
			try {
				const result = await login()
				console.log('login ‰∫ëÂáΩÊï∞ËøîÂõû:', result)
				
				if (result.success) {
					// ÁôªÂΩïÊàêÂäü
					this.isLoggedIn = true
					this.loadUserInfo()
					console.log('ÁôªÂΩïÊàêÂäüÔºåÂ∑≤Êõ¥Êñ∞Áî®Êà∑‰ø°ÊÅØ')
					uni.showToast({
						title: 'ÁôªÂΩïÊàêÂäü',
						icon: 'success',
						duration: 1500
					})
				} else {
					// ÁôªÂΩïÂ§±Ë¥•ÔºöÂΩìÂâç openid Êú™ÁªëÂÆö‰ªª‰ΩïÁî®Êà∑ÔºåÂú®Êú¨È°µÂºπÂá∫ÁªëÂÆöÂæÆ‰ø°Âè∑ÂØπËØùÊ°Ü
					console.log('ÁôªÂΩïÂ§±Ë¥•ÔºåÂáÜÂ§áÂºπÂá∫ÁªëÂÆöÂæÆ‰ø°Âè∑ÂØπËØùÊ°ÜÔºåÂéüÂõ†:', result && result.message)
					this.isLoggedIn = false
					this.userInfo = {
						name: 'Êú™ÁôªÂΩï',
						role: 'ËØ∑ÂÖàÁôªÂΩï',
						roleText: 'ËØ∑ÂÖàÁôªÂΩï',
						department: 'Âåó‰∫¨Ê¨¢‰πêË∞∑ÂåªÂä°ÂÆ§',
						avatarUrl: '',
						wechatAvatarUrl: ''
					}
					this.wechatIdInput = ''
					this.showWechatBindDialog = true
				}
			} catch (err) {
				console.error('ÁôªÂΩïÂ§±Ë¥•:', err)
				this.isLoggedIn = false
				this.userInfo = {
					name: 'Êú™ÁôªÂΩï',
					role: 'ÁôªÂΩïÂ§±Ë¥•',
					department: 'Âåó‰∫¨Ê¨¢‰πêË∞∑ÂåªÂä°ÂÆ§'
				}
				
				uni.showToast({
					title: 'ÁôªÂΩïÂ§±Ë¥•ÔºåËØ∑ÈáçËØï',
					icon: 'none',
					duration: 2000
				})
			} finally {
				this.isLoading = false
			}
		},

		// ÂèñÊ∂àÁªëÂÆöÂæÆ‰ø°Âè∑
		cancelWechatBind() {
			this.showWechatBindDialog = false
			this.wechatIdInput = ''
		},
		// Á°ÆËÆ§ÁªëÂÆöÂæÆ‰ø°Âè∑Âπ∂ÈáçËØïÁôªÂΩï
		async confirmWechatBind() {
			if (!this.wechatIdInput) {
				uni.showToast({
					title: 'ËØ∑ËæìÂÖ•ÂæÆ‰ø°Âè∑',
					icon: 'none'
				})
				return
			}
			this.isLoading = true
			try {
				const result = await login(this.wechatIdInput.trim())
				if (result.success) {
					this.isLoggedIn = true
					this.showWechatBindDialog = false
					this.loadUserInfo()
					uni.showToast({
						title: 'ÁªëÂÆöÂπ∂ÁôªÂΩïÊàêÂäü',
						icon: 'success'
					})
				} else {
					uni.showToast({
						title: result.message || 'ÁªëÂÆöÂ§±Ë¥•ÔºåËØ∑ËÅîÁ≥ªÁÆ°ÁêÜÂëò',
						icon: 'none'
					})
				}
			} catch (err) {
				console.error('ÁªëÂÆöÂæÆ‰ø°Âè∑Â§±Ë¥•:', err)
				uni.showToast({
					title: 'ÁªëÂÆöÂ§±Ë¥•ÔºåËØ∑ÈáçËØï',
					icon: 'none'
				})
			} finally {
				this.isLoading = false
			}
		},
		
		// Âä†ËΩΩÁî®Êà∑‰ø°ÊÅØ
		loadUserInfo() {
			const userInfo = getUserInfo()
			if (userInfo) {
				this.userInfo = {
					name: userInfo.realName || userInfo.name || 'Êú™ËÆæÁΩÆ',
					// Ëã±ÊñáËßíËâ≤ÂÄºÔºàadmin/project_manager/doctor/viewerÔºâÔºåÂ§áÁî®
					role: userInfo.role,
					// ‰∏≠ÊñáËßíËâ≤ÊñáÊú¨ÔºàÁÆ°ÁêÜÂëò/È°πÁõÆÁªèÁêÜ/ÂåªÂä°‰∫∫Âëò/Êü•ÁúãËÄÖÔºâÔºåÁî®‰∫éÂ±ïÁ§∫
					roleText: userInfo.roleText || 'Êú™Áü•ËßíËâ≤',
					department: 'Âåó‰∫¨Ê¨¢‰πêË∞∑ÂåªÂä°ÂÆ§'
				}
			} else {
				this.userInfo = {
					name: 'Êú™ÁôªÂΩï',
					role: '',
					roleText: 'ËØ∑ÂÖàÁôªÂΩï',
					department: 'Âåó‰∫¨Ê¨¢‰πêË∞∑ÂåªÂä°ÂÆ§'
				}
			}
		},
		
		// ÊâãÂä®ÁôªÂΩï
		async handleLogin() {
			console.log('handleLogin Ë¢´ÁÇπÂáª')
			// ÊâãÂä®ÁÇπÂáªÁôªÂΩïÊó∂ÔºåÊ∏ÖÈô§Êú¨Âú∞ÊóßÁöÑÁôªÂΩïÁºìÂ≠òÔºåÂº∫Âà∂ÈáçÊñ∞Ëµ∞‰∫ëÂáΩÊï∞ÁôªÂΩï
			try {
				if (typeof uni !== 'undefined') {
					uni.removeStorageSync('isLogin')
					uni.removeStorageSync('userInfo')
					uni.removeStorageSync('userRole')
					uni.removeStorageSync('userId')
				}
			} catch (e) {
				console.log('Ê∏ÖÈô§Êú¨Âú∞ÁôªÂΩïÁºìÂ≠òÂ§±Ë¥•:', e)
			}
			this.isLoading = true
			await this.checkAndLogin()
		},

		// ÈÄÄÂá∫ÁôªÂΩï
		logoutUser() {
			try {
				if (typeof uni !== 'undefined') {
					uni.removeStorageSync('isLogin')
					uni.removeStorageSync('userInfo')
					uni.removeStorageSync('userRole')
					uni.removeStorageSync('userId')
				} else if (typeof wx !== 'undefined') {
					wx.removeStorageSync('isLogin')
					wx.removeStorageSync('userInfo')
					wx.removeStorageSync('userRole')
					wx.removeStorageSync('userId')
				}
			} catch (e) {
				console.log('ÈÄÄÂá∫ÁôªÂΩïÊó∂Ê∏ÖÈô§ÁºìÂ≠òÂ§±Ë¥•:', e)
			}
			this.isLoggedIn = false
			this.userInfo = {
				name: 'Êú™ÁôªÂΩï',
				role: '',
				roleText: 'ËØ∑ÂÖàÁôªÂΩï',
				department: 'Âåó‰∫¨Ê¨¢‰πêË∞∑ÂåªÂä°ÂÆ§',
				avatarUrl: '',
				wechatAvatarUrl: ''
			}
			this.avatarLoadError = false
			this.wechatIdInput = ''
			this.showWechatBindDialog = false
			uni.showToast({
				title: 'Â∑≤ÈÄÄÂá∫ÁôªÂΩï',
				icon: 'none'
			})
		},
		
		updateTime() {
			const now = new Date()
			// Ê†ºÂºèÂåñ‰∏∫Ôºö2025/11/9
			const year = now.getFullYear()
			const month = now.getMonth() + 1
			const day = now.getDate()
			this.lastUpdateTime = `${year}/${month}/${day}`
		},
		
		goToPage(url) {
			// Ê£ÄÊü•ÁôªÂΩïÁä∂ÊÄÅ
			if (!this.isLoggedIn) {
				uni.showModal({
					title: 'ÈúÄË¶ÅÁôªÂΩï',
					content: 'ËØ∑ÂÖàÁôªÂΩïÂêéÂÜç‰ΩøÁî®Ê≠§ÂäüËÉΩ',
					showCancel: false,
					confirmText: 'ÂéªÁôªÂΩï',
					confirmColor: '#667eea',
					success: () => {
						this.handleLogin()
					}
				})
				return
			}
			
			// Âà§Êñ≠ÊòØÂê¶ÊòØtabbarÈ°µÈù¢
			const tabbarPages = ['/pages/index/index', '/pages/stock/index', '/pages/record/index', '/pages/user/index']
			const isTabbar = tabbarPages.includes(url)
			
			if (isTabbar) {
				uni.switchTab({
					url: url,
					fail: (err) => {
						console.log('È°µÈù¢Ë∑≥ËΩ¨Â§±Ë¥•:', err)
						uni.showToast({
							title: 'Ë∑≥ËΩ¨Â§±Ë¥•',
							icon: 'none'
						})
					}
				})
			} else {
				uni.navigateTo({
					url: url,
					fail: (err) => {
						console.log('È°µÈù¢Ë∑≥ËΩ¨Â§±Ë¥•:', err)
						uni.showToast({
							title: 'È°µÈù¢ÂºÄÂèë‰∏≠',
							icon: 'none'
						})
					}
				})
			}
		},
		
		showComingSoon() {
			uni.showModal({
				title: 'ÂäüËÉΩÂç≥Â∞ÜÊé®Âá∫',
				content: 'ËØ•ÂäüËÉΩÊ≠£Âú®ÂºÄÂèë‰∏≠ÔºåÊï¨ËØ∑ÊúüÂæÖÔºÅ',
				showCancel: false,
				confirmText: 'Áü•ÈÅì‰∫Ü',
				confirmColor: '#667eea'
			})
		},
		
		goToUserManage() {
			// ‰∏¥Êó∂ÊñπÊ°àÔºöË∑≥ËΩ¨Âà∞ËÆæÁΩÆÈ°µÈù¢ÊàñÊòæÁ§∫ÊèêÁ§∫
			uni.showToast({
				title: 'Áî®Êà∑ÁÆ°ÁêÜÂäüËÉΩÂºÄÂèë‰∏≠',
				icon: 'none'
			})
		},
		
		showAbout() {
			uni.showModal({
				title: 'ÂÖ≥‰∫éÁ≥ªÁªü',
				content: 'Âåó‰∫¨Ê¨¢‰πêË∞∑ÂåªÂä°ÂÆ§ÁÆ°ÁêÜÁ≥ªÁªü\n\nÁâàÊú¨Ôºöv1.1.2\nÂºÄÂèëËÄÖÔºö‰∫éÂª∫ÂçéÔºàÂæÆ‰ø°ÔºöbjkfjzÔºâ',
				showCancel: false,
				confirmText: 'Á°ÆÂÆö',
				confirmColor: '#667eea'
			})
		},
		
		// ÊòæÁ§∫Â§¥ÂÉèÈÄâÈ°π
		showAvatarOptions() {
			if (!this.isLoggedIn) {
				uni.showToast({
					title: 'ËØ∑ÂÖàÁôªÂΩï',
					icon: 'none'
				})
				return
			}
			
			uni.showActionSheet({
				itemList: ['‰ªéÁõ∏ÂÜåÈÄâÊã©', 'ÊãçÁÖß'],
				success: (res) => {
					if (res.tapIndex === 0) {
						this.chooseImageFromAlbum()
					} else if (res.tapIndex === 1) {
						this.takePhoto()
					}
				}
			})
		},
		
		// ‰ªéÁõ∏ÂÜåÈÄâÊã©
		async chooseImageFromAlbum() {
			await this.chooseAndUploadImage(['album'])
		},
		
		// ÊãçÁÖß
		async takePhoto() {
			await this.chooseAndUploadImage(['camera'])
		},
		
		// ÈÄâÊã©Âπ∂‰∏ä‰º†ÂõæÁâá
		async chooseAndUploadImage(sourceType) {
			try {
				const res = await uni.chooseImage({
					count: 1,
					sizeType: ['compressed'],
					sourceType: sourceType
				})
				
				uni.showLoading({
					title: '‰∏ä‰º†‰∏≠...'
				})
				
				const tempFilePath = res.tempFilePaths[0]
				
				// ‰∏ä‰º†Âà∞‰∫ëÂ≠òÂÇ®
				const cloudPath = await this.uploadToCloud(tempFilePath)
				
				// Êõ¥Êñ∞Êï∞ÊçÆÂ∫ì
				await this.updateAvatar(cloudPath, 'custom')
				
				uni.hideLoading()
				uni.showToast({
					title: 'Â§¥ÂÉèÂ∑≤Êõ¥Êñ∞',
					icon: 'success'
				})
			} catch (err) {
				console.error('‰∏ä‰º†Â§±Ë¥•Ôºö', err)
				uni.hideLoading()
				if (err.errMsg && !err.errMsg.includes('cancel')) {
					uni.showToast({
						title: '‰∏ä‰º†Â§±Ë¥•',
						icon: 'error'
					})
				}
			}
		},
		
		// ‰∏ä‰º†Âà∞‰∫ëÂ≠òÂÇ®
		async uploadToCloud(filePath) {
			const cloudPath = `avatars/${Date.now()}-${Math.random().toString(36).substr(2, 9)}.jpg`
			
			const result = await wx.cloud.uploadFile({
				cloudPath: cloudPath,
				filePath: filePath
			})
			
			return result.fileID
		},
		
		// Êõ¥Êñ∞Â§¥ÂÉè
		async updateAvatar(avatarUrl, type) {
			const res = await wx.cloud.callFunction({
				name: 'updateMyInfo',
				data: {
					[type === 'wechat' ? 'wechatAvatarUrl' : 'avatarUrl']: avatarUrl
				}
			})
			
			// Êõ¥Êñ∞Êú¨Âú∞
			if (type === 'wechat') {
				this.userInfo.wechatAvatarUrl = avatarUrl
			} else {
				this.userInfo.avatarUrl = avatarUrl
			}
			
			this.avatarLoadError = false
			
			// Êõ¥Êñ∞ÁºìÂ≠ò
			const storedUserInfo = uni.getStorageSync('userInfo')
			if (storedUserInfo) {
				if (type === 'wechat') {
					storedUserInfo.wechatAvatarUrl = avatarUrl
				} else {
					storedUserInfo.avatarUrl = avatarUrl
				}
				uni.setStorageSync('userInfo', storedUserInfo)
			}
		},
		
		// Â§¥ÂÉèÂä†ËΩΩÂ§±Ë¥•
		onAvatarError() {
			console.log('Â§¥ÂÉèÂä†ËΩΩÂ§±Ë¥•')
			this.avatarLoadError = true
		}
	}
}
</script>

